html {
  height: 100%;
}
body {
  margin: 0;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.page {
  flex: 1;
  width: 100%;
}

#container {
  width: var(--canvas-size);
  height: 100%;
  display: flex;
  flex-direction: column;
  font-size: larger;
}

#canvas-container {
  position: relative;
  width: var(--canvas-size);
  height: var(--canvas-size);
  border: 1px solid gray;
}

.canvas {
  position: absolute;
  width: var(--canvas-size);
  height: var(--canvas-size);
  top: 0;
  left: 0;
}

#canvas {
  z-index: 0;
}
#pointer-canvas {
  z-index: 1;
}

#draw-button {
  background: -moz-linear-gradient(90deg in hsl longer hue, hsl(0, 100%, 50%), hsl(365 100 50));
  background: -webkit-linear-gradient(90deg in hsl longer hue, hsl(0, 100%, 50%), hsl(365 100 50));
  background: linear-gradient(90deg in hsl longer hue, hsl(0, 100%, 50%), hsl(365 100 50));
  font-size: 10vw;
  user-select: none;
  width: 100%;
  height: 400px;
}

#permission {
  width: 100%;
  height: 400px;
  display: none;
  font-size: 10vw;
  text-align: center;
  margin: auto;
}

#calibrate-button {
  font-size: 10vw;
  user-select: none;
  width: 100%;
  height: 400px;
}

.big-text {
  font-size: 200px;
}
